/* Each painting group of information */

.Gallery_allPaintings__1nzgh div {
    display: flex;
    flex-direction: column;
    flex: 0 0 33.333333%;
    padding-bottom: 2%;
}

/* Painting */

.Gallery_allPaintings__1nzgh div img {
    width: auto;
    max-width: 80%;
    max-height: 500px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    transition: all .2s ease-in-out;
}

/* Painting on hover */

.Gallery_allPaintings__1nzgh div img:hover {
    transform: scale(1.1);
    box-shadow: 0 0 30px #888888;
}
